import React, {Component} from 'react'
import {
    Cell,
    CellButton,
    FormLayout,
    Group, Slider
} from "@vkontakte/vkui";
import Cookie from "../cookie/Cookie";


export default class Profile extends Component {
    constructor(props) {
        super(props);
        let endFromCookie = Cookie.getEndOfDay();
        let countFromCookie = Cookie.getCountOfActivities();
        this.state = {
            activeModal: null,
            end: endFromCookie,
            countOfActivities: countFromCookie
        }
    }

    componentWillUnmount() {
        Cookie.setEndOfDay(this.state.end);
        Cookie.setCountOfActivities(this.state.countOfActivities);
        window.location.reload();
    }

    static prettyEnd(end)
    {
        if(end >= 22)
        {
            return " "+ end + " ";
        }
        if(end === 21)
        {
            return " 21 ";
        }
        return " " + end + " ";
    }

    static prettyCount(countStr)
    {
        let count = parseInt(countStr);
        switch (count) {
            case 1:
                return " ";
            case 2:
                return " ";
            case 3:
                return " ";
            case 4:
                return " ";
            case 5:
                return " ";
            case 6:
                return " ";
            case 7:
                return " ";
            case 8:
                return " ";
            case 9:
                return " ";
            case 10:
                return " ";
            default:
                return count + " ";
        }
    }

    render() {
        return (
            <Group>
                <FormLayout>
                    <Slider
                        step={1}
                        min={14}
                        max={23}
                        value={this.state.end}
                        top={"      ?   " + Profile.prettyEnd(this.state.end)}
                        onChange={value => this.setState({end: value})}
                    />
                    <Slider
                        step={1}
                        min={2}
                        max={10}
                        value={this.state.countOfActivities}
                        top={"      ?   " + Profile.prettyCount(this.state.countOfActivities)}
                        onChange={value => this.setState({countOfActivities: value})}
                    />
                </FormLayout>


                <CellButton onClick={() => {
                    this.setState({activeModal: "delete"})
                }}>
                     
                </CellButton>

                {this.state.activeModal === "delete" ? (<div>
                    <Cell>?</Cell>
                    <CellButton onClick={() => {
                        this.setState({activeModal: null});
                    }}>
                        
                    </CellButton>
                    < CellButton mode={"destructive"} onClick={() => {
                        Cookie.deleteStats();
                        this.setState({activeModal: null});
                    }}>
                        
                    </CellButton>
                </div>) : <div/>}

            </Group>


        );
    }

}


